<template>
  <a-modal
    :title="title"
    :width="640"
    :visible="visible"
    :confirmLoading="loading"
    @ok="
      () => {
        $emit('ok')
      }
    "
    @cancel="
      () => {
        $emit('cancel')
      }
    "
  >
    <a-spin :spinning="loading">
      <a-form-model ref="thirdPartyForm" :model="model" :rules="rules" v-bind="formItemLayout">
        <a-form-model-item label="合作方名称" prop="partner_name" help="">
          <a-input placeholder="" v-model="model.partner_name" />
        </a-form-model-item>
        <a-form-model-item label="有效期" prop="access_key" help="平台使用有效期(不填表示长期有效)">
          <a-date-picker
            v-model="model.expired_time"
            style="width: 100%"
            valueFormat="YYYY-MM-DD"
            :inputReadOnly="true"
          />
        </a-form-model-item>
        <a-form-model-item label="服务ip" prop="server_ip" help="默认：127.0.0.1">
          <a-input placeholder="" v-model="model.server_ip" />
        </a-form-model-item>
        <a-form-model-item label="接入平台" prop="live_providers" help="">
          <a-select mode="multiple" v-model="model.live_providers">
            <a-select-option v-for="(value, key) in liveProviderItems" :value="key" :key="key">{{
              value
            }}</a-select-option>
          </a-select>
        </a-form-model-item>
        <a-form-model-item label="额外参数" prop="params" :help="paramsHelp">
          <a-textarea
            v-model="model.params"
            placeholder="接入平台参数（标准json字符串，可以在sojson.com网站内编辑后复制过来）"
            :auto-size="{ minRows: 4, maxRows: 10 }"
          />
        </a-form-model-item>
      </a-form-model>
    </a-spin>
  </a-modal>
</template>

<script>
import { getLiveProviderItems } from '@/api/third-party'

const paramsHelp = '例：{"iSecureCenter":{"isOnline":false,"appkey":"***","secret":"*****","localAppkey":"***","localSecret":"***","ip":"125.68.2.1","port":10443,"localIp":"192.168.3.2","localPort":443,"enableHTTPS":1,"language":"zh_CN","layout":"2x2","playMode":0,"reconnectDuration":5,"reconnectTimes":5,"showSmart":0,"showToolbar":1,"toolBarButtonIDs":"0,16,256,257,258,259,260,512,513,514,515,516,517,768,769","snapDir":"/data/snap","videoDir":"/data/video"},"Ys7":{"apiUrl":"https://open.ys7.com/api","appKey":"** ** ** ** * ","appSecret":"*********","tokenTtl":364000,"defaultShowChannel":32}}'
const defaultModel = {
    partner_name: '',
    live_providers: ['BLive'],
    expired_time: '',
    server_ip: '127.0.0.1'
}
export default {
    props: {
        visible: {
            type: Boolean,
            default: true
        },
        loading: {
            type: Boolean,
            default: () => false
        }
    },
    components: {},
    data () {
        return {
            title: '创建合作方',
            model: defaultModel,
            liveProviderItems: {},
            formItemLayout: {
                labelCol: { span: 6 },
                wrapperCol: { span: 14 }
            },
            rules: {
                partner_name: [{ required: true, message: '合作方名称必须填写', trigger: 'blur' }],
                live_providers: [{ required: true, message: '接入平台必须选择', trigger: 'blur' }]
            },
            paramsHelp: paramsHelp
        }
    },
    computed: {},
    methods: {
        resetForm () {
            this.model = defaultModel
            this.$nextTick(() => {
               this.$refs.thirdPartyForm.resetFields()
            })
        },
        async getLiveProviderItems () {
            this.liveProviderItems = await getLiveProviderItems()
            console.log('====================================')
            console.log('this.liveProviderItems:', this.liveProviderItems)
            console.log('====================================')
        }
    },
    watch: {
        async visible (newValue, oldValue) {
            if (newValue) {
                this.getLiveProviderItems()
            }
        }
    }
}
</script>
<style lang="less" scoped>
.ant-select {
    min-width: 220px;
}
</style>
